<template>
  <el-container>
    <el-header><div class="grid-content bg-purple-dark">山洪预警系统</div></el-header>
    <el-main>
      <el-table
    :data="tableData"
    height="700"
    border
    style="width: 100%"
    :row-class-name="tableRowClassName">    
    <el-table-column
      label="站点名称"
      width="180">
      <template slot-scope="scope">        
        <el-popover trigger="hover" placement="top">          
          <p>站点: {{ scope.row.name }}</p>
          <p>乡镇: {{ scope.row.region }}</p>
          <div slot="reference" class="name-wrapper">
            <i class="el-icon-s-home"></i>
            <el-tag size="medium">{{ scope.row.name }}</el-tag>
          </div>
        </el-popover>
      </template>
    </el-table-column>
    <el-table-column label="0.5小时雨量">
      <template slot-scope="scope">
        
        <el-button
          size="mini"          
          @click="handleHalf(scope.$index, scope.row)">{{scope.row.half_rain}}</el-button>
      </template>
    </el-table-column>

    <el-table-column label="1小时雨量">
      <template slot-scope="scope">        
        <el-button
          size="mini"          
          @click="handleOne(scope.$index, scope.row)">{{scope.row.one_rain}}</el-button>
      </template>
    </el-table-column>

    <el-table-column label="1.5小时雨量">
      <template slot-scope="scope">        
        <el-button
          size="mini"          
          @click="handleOneHalf(scope.$index, scope.row)">{{scope.row.one_half_rain}}</el-button>
      </template>
    </el-table-column>

    <el-table-column label="2小时雨量">
      <template slot-scope="scope">        
        <el-button
          size="mini"          
          @click="handleTwo(scope.$index, scope.row)">{{scope.row.two_rain}}</el-button>
      </template>
    </el-table-column>

    <el-table-column label="3小时雨量">
      <template slot-scope="scope">        
        <el-button
          size="mini"          
          @click="handleThree(scope.$index, scope.row)">{{scope.row.three_rain}}</el-button>
      </template>
    </el-table-column>

  </el-table>
    </el-main>
    <el-footer>
              <i class="el-icon-light-rain" style="color:#f0f9eb"></i>
              <i class="el-icon-right"></i>
              <i class="el-icon-heavy-rain" style="color:#f9ebeb" ></i></el-footer>
  </el-container>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },
  methods:{
    tableRowClassName(row) {
        let a = Number(row.three_rain);
        console.log(row.three_rain);
        if ( a > 0) 
        { return 'rain-row';}                
      },
      handleHalf(index, row) {
        console.log(index, row)
      },
      handleOne(index, row) {
        console.log(index, row)
      },
      handleOneHalf(index, row) {
        console.log(index, row)
      },
      handleTwo(index, row) {
        console.log(index, row)
      },
      handleThree(index, row) {
        console.log(index, row)
      },
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



