<template>
  <div id="app">
    <el-container>
      <el-header
        ><div class="grid-content bg-purple-dark">山洪预警系统</div></el-header
      >
      <el-main>
        <el-dialog title="收货地址" :visible.sync="dialogVisible" width="700px"
          ><div id="echart_view" style="width: 695px;height:400px;"></div>
        </el-dialog>

        <el-table
          :data="tableData"
          height="700"
          border
          style="width: 100%"
          :row-class-name="tableRowClassName"
        >
          <el-table-column label="站点名称" width="180">
            <template slot-scope="scope">
              <el-popover trigger="hover" placement="top">
                <p>站点: {{ scope.row.name }}</p>
                <p>乡镇: {{ scope.row.region }}</p>
                <div slot="reference" class="name-wrapper">
                  <i class="el-icon-s-home"></i>
                  <el-tag size="medium">{{ scope.row.name }}</el-tag>
                </div>
              </el-popover>
            </template>
          </el-table-column>

          <el-table-column label="0.5小时雨量">
            <template slot-scope="scope">
              <el-button
                size="mini"
                @click="handleHalf(scope.$index, scope.row)"
                >{{ scope.row.half_rain }}</el-button
              >
            </template>
          </el-table-column>

          <el-table-column label="1小时雨量">
            <template slot-scope="scope">
              <el-button
                size="mini"
                @click="handleOne(scope.$index, scope.row)"
                >{{ scope.row.one_rain }}</el-button
              >
            </template>
          </el-table-column>

          <el-table-column label="1.5小时雨量">
            <template slot-scope="scope">
              <el-button
                size="mini"
                @click="handleOneHalf(scope.$index, scope.row)"
                >{{ scope.row.one_half_rain }}</el-button
              >
            </template>
          </el-table-column>

          <el-table-column label="2小时雨量">
            <template slot-scope="scope">
              <el-button
                size="mini"
                @click="handleTwo(scope.$index, scope.row)"
                >{{ scope.row.two_rain }}</el-button
              >
            </template>
          </el-table-column>

          <el-table-column label="3小时雨量">
            <template slot-scope="scope">
              <el-button
                size="mini"
                @click="handleThree(scope.$index, scope.row)"
                >{{ scope.row.three_rain }}</el-button
              >
            </template>
          </el-table-column>
        </el-table>
      </el-main>
      <el-footer>
        <i class="el-icon-light-rain" style="color: #f0f9eb"></i>
        <i class="el-icon-right"></i>
        <i class="el-icon-heavy-rain" style="color: #f9ebeb"></i
      ></el-footer>
    </el-container>
  </div>
</template>

<script>
import Axios from "axios";
import * as echarts from 'echarts/core';
// 引入柱状图图表，图表后缀都为 Chart
import {
    LineChart
} from 'echarts/charts';
// 引入提示框，标题，直角坐标系组件，组件后缀都为 Component
import {
    TitleComponent,
    TooltipComponent,
    GridComponent
} from 'echarts/components';
// 引入 Canvas 渲染器，注意引入 CanvasRenderer 或者 SVGRenderer 是必须的一步
import {
    CanvasRenderer
} from 'echarts/renderers';

// 注册必须的组件
echarts.use(
    [TitleComponent, TooltipComponent, GridComponent, LineChart, CanvasRenderer]
);
export default {
  name: "App",
  data() {
    return {
      tableData: [],
      halfData:{},
      dialogVisible: false,
    };
  },
  async mounted() {
    await this.table_data();
  },
  methods: {
    async table_data() {
      const response = await Axios.get("/api/mt_current");
      this.tableData = response.data;
    },
    tableRowClassName({ row, rowIndex }) {
      let a = Number(row.three_rain);
      if (a > 0) {
        return "rain-row";
      }
    },
    async getHalfRain(dev_id) {
      const response = await Axios.get("/api/half_rain",{
        params:{
          dev_id,
        }
      });
      this.halfData = response.data;
    },
    async handleHalf(index, row) {
      this.dialogVisible = true;
      await this.getHalfRain(row.id);
      var myChart = echarts.init(document.getElementById("echart_view"));
      myChart.setOption({
        title: {
          text: "0.5小时降雨量",
        },
        tooltip: {},
        xAxis: {
          type: 'category',
          boundaryGap: false,
          data: this.halfData.times,
        },
        yAxis: {
          type: 'value'
        },
        series: [
          {
            name: "雨量",
            type: "line",
            data: this.halfData.values,
          },
        ],
      });
    },
    handleOne(index, row) {
      console.log(index, row);
    },
    handleOneHalf(index, row) {
      console.log(index, row);
    },
    handleTwo(index, row) {
      console.log(index, row);
    },
    handleThree(index, row) {
      console.log(index, row);
    },
  },
};
</script>

<style>
.el-header {
  background-color: #9fc3e7;
  color: rgb(1, 6, 20);
  text-align: center;
  line-height: 60px;
  font-size: 20pt;
}

.el-footer {
  background-color: #9fc3e7;
  text-align: center;
  line-height: 60px;
  font-size: 20pt;
}
.el-aside {
  background-color: #d3dce6;
  color: #333;
  text-align: center;
  line-height: 200px;
}

.el-main {
  background-color: #e9eef3;
  color: #333;
  text-align: center;
  line-height: 30px;
}

.el-table .warning-row {
  background: oldlace;
}
.el-table .one-row {
  background: oldlace;
}

.el-table .rain-row {
  background: #f0f9eb;
}

.el-table .half-row {
  background: #f9f7eb;
}

.el-table .onehalf-row {
  background: #f9f1eb;
}

.el-table .two-row {
  background: #f9eeeb;
}

.el-table .three-row {
  background: #f9ebeb;
}
</style>



